@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{cms-ns}-category{
	background: #fff;
	overflow: hidden;
	padding-bottom: 10px;
	border-top:1px @container-bg-color solid;
	.item{
		width:20%;
		float: left;
		text-align: center;
		a{
			display: block;
			padding-top: 10px;
		}
		.logo{
			width: 54%;
			display: block;
			margin:0 auto 5px;
		}
		p{
			font-size: @font-size-base;
			line-height:1.6;
			color:#333;
			.ellipsis();
		}
	}
}
