@import "../_store-variables.less";
@import "../_store-mixins.less";

.fn-@{cms-ns}-complex-group{
	overflow: hidden;

	.item-big{
		width:50%;
		float: left;

		&.img-box:before{
			padding-top:59%;
		}

		&:first-child{
			&:after{
				display: block;
				content:'';
				.size(1px,100%);
				background: @container-bg-color;
				.absolute(0,0,_,_);
			}
		}
	}
	.item{
		width:20%;
		border-top:1px @container-bg-color solid;
		position: relative;
		float: left;
		background: #fff;
		
		&:after{
			display: block;
			content:'';
			.size(1px,100%);
			background: @container-bg-color;
			.absolute(0,0,_,_);
		}
		&:before{
			display: block;
			content:'';
			.size(100%,100%);
			padding-top: 100%;
			padding-bottom: 50px;	
		}
		
		&:last-child{
			&:after{
				display: none;
			}
		}

		.item-wrap{
			.size(100%,100%);
			.absolute(0,_,_,0);	
		}

		.pic{
			display: block;
			width:80%;
			margin:13% auto 0;
		}
		.text{
			line-height: 1.6;
			height:unit(1.6 * @font-size-base,rem);
			.ellipsis();
			text-align: center;
			font-size: @font-size-base;
		}
		.tips{
			width:90%;
			margin:5px auto 10px;
			padding:0 2px;
			line-height: 1.6;
			font-size: @font-size-sm;
			background: #ff2951;
			color:#fff;
			border-radius: 10px;
			text-align: center;
			.ellipsis();
		}
	}
}
@media(max-width:@screen-xs-min){
	.cms-page .fc-m-complex-group .item .tips{
		font-size: @font-size-sm;
		padding:0;
		width:100%;
	}
}
