@import "../_store-variables.less";
@import "../_store-mixins.less";

.fn-@{cms-ns}-pic-group{

	border-top:1px @container-bg-color solid;

	.wrapper{
		position: relative;

		&:before{
			padding-top: 42.8%;
			content: '';
			display: block;
		}
	}

	.item{
		.absolute(_,_,_,_);
		width: 50%;

		&.img-box:before{
			padding-top:42.8%;
		}

		&-l{
			height: 100%;
			left: 0;
			top: 0;
			&.img-box:before{
				padding-top:85.6%;
			}

			&:after{
				display: block;
				content:'';
				.size(1px,100%);
				background: @container-bg-color;
				.absolute(0,0,_,_);
			}
		}

		&-rt{
			height: 50%;
			right: 0;
			top: 0;
			&:after{
				display: block;
				content:'';
				.size(100%,1px);
				background: @container-bg-color;
				.absolute(_,_,0,0);
			}
		}

		&-rb{
			height: 50%;
			right: 0;
			bottom: 0;
		}
	}
}
