@import "../_store-variables.less";
@import "../_store-mixins.less";

.fn-@{cms-ns}-label-line{

	width: 93.6%;
	margin:3.2% auto;
	.labels{
		margin-top: 1.5%;

		&:first-child{
			margin-top: 0;
		}

		a{
			font-size: 14px;
			text-align: center;
			margin-right: 3.2%;
			background: #fff;
			border: 1px solid @border-color-base;
			.ellipsis();
			padding: 5px 0;

			&:last-child{
				margin-right: 0;
			}
		}

		&-3 a{
			width: 31.2%;
		}
	}
}
