@import "../_store-variables.less";
@import "../_store-mixins.less";

.fn-@{cms-ns}-headline{
	.headline{
		@head-height: 22;
		@head-padding: 14;
		@left-width: 92;

		padding: unit(@head-padding, px) 0;
		height: unit(@head-height + @head-padding * 2, px);
		background: #fff;

		.pic{
			.size(unit(@left-width, px), unit(@head-height, px));
			float: left;
			text-align: right;

			&:before{
				background: data-uri('@{cms-image-dir}/store_headline_bg.png') no-repeat 0 0;
				display: inline-block;
				vertical-align: top;
				content: '';
				.size(unit(@left-width - 10, px), unit(@head-height, px));
				background-size: 100% 100%;
			}
		}

		.list{
			margin-left: unit(@left-width + 10, px);
			border-left:1px @border-color-base solid;
			.headline-swiper{
				height: unit(@head-height, px);

				.swiper-slide{
					a {
						display: block;
						margin-right:5px;
						line-height: 1.6;
						padding-left: 10px;
						position: relative;
						.ellipsis();
						color:#999;

						.hot{
							border:1px #ff0000 solid;
							color:#ff0000;
							margin-right:3px;
							padding:0 2px;
							font-size: @font-size-base;
							border-radius: 2px;
						}
					}
				}
			}
		}
	}
}
