@import "../_store-variables.less";
@import "../_store-mixins.less";


.fn-@{cms-ns}-header{
	position: relative;

		&:before{
			display: block;
			content:'';
			padding-top:9.4%;
		}

		.text{
			.absolute(50%,_,_,50%);
			transform: translate(-50%,-50%);
			z-index:5;
		}
		.img{
			.absolute(0,_,_,0);
			.size(100%);
			z-index:2;
		}
}
